.App {
  font-family: sans-serif;
  text-align: center;
}
* {
  width: 100%;
  height: 100%;
}
button {
  background-color: #e52a30;
  color: #ffffff;
  font-size: 25px;
  font: bold;
  width: 31.7%;
  height: 6.5%;
  border: none;
  border-radius: 71px;
  margin-top: 27px;
}
img.Logo {
  width: 12.8%;
  height: auto;
  margin-bottom: 2%;
}
img.SocialMedia {
  padding-bottom: 2px;
  margin-left: 23px;
  width: 2.7%;
  height: auto;
}
img.SocialMediaBig {
  margin-top: 37px;
  margin-left: 23px;
  width: 3.1%;
  height: auto;
}
p.Sponsor {
  float: left;
  margin-top: 7%;
  color: #a09797;
  font-size: 25px;
  font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
}
p.Buttons {
  float: left;
  margin-top: 5.5%;
}
p.Video {
  float: right;
  margin-right: 8%;
  margin-top: 6.1%;
  width: 35%;
  height: auto;
}
